                </tbody>
            </table>
        </div>
        <!-- leads-table-area  -->

        <!-- leads-assign area  -->
        <div class="modal fade" id="leadAssign" tabindex="-1" role="dialog">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">Lead Assign</h5>
                    <a class="close" data-dismiss="modal" aria-label="Close" >
                    <span aria-hidden="true"><img class="img-fluid" src="../../assets/img/23. Closex.svg" alt=""></span>
                    </a>
                </div>
                <form v-on:submit.prevent = "assignToUser">
                    <div class="modal-body">
                        <div class="add-member add-lead">
                            <div class="form-row">
                                <div class="form-group col-md-12">
                                    <label for="inputEmail4">Users</label>
                                    <Select2 v-model="assign.to" :options="allEmployees" :settings="{placeholder: 'Select user'}" />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <a href="#" class="add-btn" data-dismiss="modal"><img class="img-fluid" src="../../assets/img/22. Delete.svg" alt=""> Cancel</a>
                        <button type="submit" class="rem-btn" ><img class="img-fluid" src="../../assets/img/23. Close.svg" alt=""> Assign</button>
                    </div>
                </form>
                </div>
            </div>
        </div>
        <!-- / leads-assign area  -->
        <input type="hidden" :value="$route.query.type" id="lead_type" />
        
    </div>
</template>

<script>
import axios from 'axios'
import $ from 'jquery'
import 'datatables.net';
import 'datatables.net-responsive';
import 'datatables.net-dt/css/jquery.dataTables.min.css';
import 'datatables.net-responsive-dt/css/responsive.dataTables.min.css';
import Select2 from 'v-select2-component';

export default {
    name: 'LeadsGrid',
    components: {Select2},
    data(){
		return{
            leads: null,
            filterSearch: {by:''},
            allEmployees:[],
            assign:{to:[],lead_id:0},
            activeTab:'',
            leadsCounter:{}
		}
	},
	created(){
        this.getAllEmployees()
        this.getLeadsCount()
        this.$root.$on('newLead', function(action) {
            if(action == 'added'){
                $('#leads_table').dataTable().fnDraw();
            }
        })

    },
    watch: {
        '$route.query.type': function () {
            $("#lead_type").val(this.$route.query.type);
            $('#leads_table').dataTable().fnDraw();
        }
    },
	methods:{
        filterData(){
            if(this.filterSearch.from != undefined && this.filterSearch.from != '' && this.filterSearch.from != 'undefined' && this.filterSearch.from != null &&
            this.filterSearch.to != undefined && this.filterSearch.to != '' && this.filterSearch.to != 'undefined' && this.filterSearch.to != null){
                if(new Date(this.filterSearch.from) < new Date(this.filterSearch.to)){
                    $('#leads_table').dataTable().fnDraw();
                }else{
                    alert('Start date must be less than End date.');
                }
            }else{
                alert("Please enter start and end date.");
            }
        },
        assignToUser(){
            //console.log(this.assignTo);
			axios
			.post('lead_assign',this.assign,{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}} )
			.then( res => {
                if (res.data.success) {
                    this.assign = {to:[],lead_id:0}
                    $("#leadAssign").modal('hide')
                }
			})
			.catch(err =>{
				console.log(err)
			})


        },
        getAllEmployees(){
			axios
			.get('all_employees',{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}} )
			.then( res => {
                const data = res.data
                if (data.success) {
                    if(data.success.length > 0){
                        var dd = [];
                        $.each(data.success, function(key, value) {
                            dd.push({id:value.id,text:value.name})
                        });
                        this.allEmployees = dd;
                    }
                }
            })
			.catch(err =>{
				console.log(err)
			})
        },
        getLeadsCount(){
            axios
			.post('leads_count',{},{headers: {Authorization:'Bearer '+this.$storage.get('auth').token}} )
			.then( res => {
                const data = res.data
                if (data.success) {
                    this.leadsCounter = data.success
                }
            })
			.catch(err =>{
				console.log(err)
			})
        }
    },
    mounted(){
        $('#leads_table').addClass('nowrap').dataTable({
            "responsive": true,
            "fixedHeader": true,
            "processing": true,
            "serverSide": true,
            ajax:{
                url:'https://webmatech.com/api/dt_leads',
                type: 'POST',
                data:function ( d ) {
                    d.from = $('#from').val();
                    d.to = $('#to').val();
                    d.by = $('#by').val();
                    d.type = $('#lead_type').val(); 
                },
                headers: { 'Authorization': 'Bearer '+this.$storage.get('auth').token },
            },
            drawCallback: function () {

                //alert( 'Table redrawn '+new Date() );
            },
            columnDefs: [
                { className: 'dt-body-right' },
                {
                    "targets": [7,8],
                    'orderable': false,
                },
            ]
            //columnDefs: [{ targets: [-1, -3], className: 'dt-body-right' }]
        });
        let vm = this
        $('tbody').on( 'click', '.assin', function(){
            var id = $(this).data("id")
            if(id !== undefined && id != 'undefined' && id != null && id != ''){
                vm.assign.lead_id = id
            }else{
                vm.assign.lead_id = 0
            }
        });
    }
}
</script>

<style scoped>
    @import '../../assets/css/dataTables.css';
</style>